.search-form {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px auto;
  background-color: #000;

  width: 70%;
  height: 50px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 1), -23px 0 20px -23px rgba(0, 0, 0, 0.8),
    23px 0 20px -23px rgba(0, 0, 0, 0.8), 0 0 40px rgba(0, 0, 0, 0.1) inset;
}

input {
  display: flex;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  width: 50%;
  height: 70%;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  font-size: 1.1em;

  color: black;
  background-color: white;
  border-color: darkgoldenrod;
  border-radius: 20px 5px 5px 20px;
  border-width: 0;
  outline: none;
}

input::placeholder {
  color: lightgray;
  font-size: 0.9em;
  text-align: center;
}

input:focus {
  border-width: 1px;
}

button {
  margin-left: 20px;
  width: 60px;
  height: 70%;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  line-height: 1.5;
  font-size: 0.9em;

  background-color: black;
  color: white;
  border-color: white;
  border-radius: 5px 20px 20px 5px;
  border-width: 0.1em;
}

button:hover {
  background-color: white;
  color: black;
  border-color: darkgoldenrod;
  border-width: 0px;
  border-radius: 5px 20px 20px 5px;
}
